<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="110px" :rules="rules">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">{{form.sale_info.work_status_name}}({{form.sale_info.stage_name}})</span></h2>
                    <p class="fs-14">当前处理人：{{form.sale_info.user_name}}</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="客户名称">
                                {{form.sale_info.name}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="电话">
                                {{form.sale_info.tel}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <!-- <el-row v-if="form.sale_info.id_card">
                        <el-col :span="12">
                            <el-form-item label="客户类型">
                                {{form.sale_info.type}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="性别">
                                {{form.sale_info.gender}}
                            </el-form-item>
                        </el-col>
                    </el-row> -->

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="证件号">
                                {{form.sale_info.credit_code}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="销售单编号">
                                {{form.sale_info.sale_num_code}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <!-- <el-col :span="12">
                            <el-form-item label="征信情况">
                                该用户曾在公司贷款{{form.sale_info.credit_count}}笔，逾期{{form.sale_info.overdue_number}}次。
                            </el-form-item>
                        </el-col> -->
                        <el-col :span="12">
                            <el-form-item label="负责销售员" prop="sale_info.sale_body_id">
                                <el-select v-model="form.sale_info.sale_body_id" placeholder="请选择" :disabled="isDisabledBase()">
                                    <el-option
                                        v-for="item in optionsSaleBody"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="是否挂靠">
                                <el-radio-group v-model="form.sale_info.is_affiliat" :disabled="isDisabledBase()">
                                    <el-radio label="1">挂靠</el-radio>
                                    <el-radio label="2">不挂靠</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12" v-if="form.sale_info.is_affiliat == 1" :disabled="isDisabledBase()">
                            <el-form-item label="选择挂靠公司">
                                <!-- {{form.sale_info.cp_affiliat_id}}
                                <el-button @click="handleSelectClient('company')">选择挂靠公司</el-button> -->
                                <el-select v-model="form.sale_info.cp_affiliat_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        
                    </el-row>
                    
                </el-collapse-item>

                <el-collapse-item title="　商品信息" name="2">
                    <div class="toolbar ta-r mb10" v-if="isShowAddBtn()">
                        <el-button type="primary" size="mini" plain @click="handleSelectCarType">添加商品</el-button>
                    </div>
                    <el-table :data="form.car_info.rows" border>
                        <el-table-column label="商品信息" width="150">
                            <template slot-scope="{row, $index}">
                                <p>{{row.car_series}} {{row.car_brand}}</p>
                                <p>{{row.horse_power}} {{row.drive_form}} {{row.car_class}}</p>
                                <p>{{row.fuel_type}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="color" label="颜色"></el-table-column>

                        <template v-if="isShowTable11()">
                            <el-table-column prop="sale_price" label="销售单价"></el-table-column>
                            <el-table-column label="数量" width="150">
                                <template slot-scope="{row}">
                                    <el-input-number size="mini" :min="1" :max="100" v-model="row.num"></el-input-number>
                                </template>
                            </el-table-column>
                            <el-table-column prop="total_price" label="小计">
                                <template slot-scope="{row, $index}">
                                    {{jijia(row)}}
                                </template>
                            </el-table-column>
                        </template>
                        
                        <template v-else-if="isShowTable12()">
                            <el-table-column prop="purchase_node" label="对应销售单"></el-table-column>
                            <el-table-column prop="vin_number" label="vin"></el-table-column>
                            <el-table-column prop="sale_price" label="销售单价"></el-table-column>
                            <el-table-column prop="num" label="数量"></el-table-column>
                            <el-table-column prop="total_price" label="小计"></el-table-column>
                        </template>

                        <template v-else>
                            <el-table-column prop="num" label="数量"></el-table-column>
                            <el-table-column prop="total_price" label="销售总额"></el-table-column>
                            <el-table-column label="交付类型" width="130px">
                                <template slot-scope="{row,$idnex}">
                                    <div class="pd5 h35 bb-da-aaa" v-for="item in row.car_list">{{item.type_name}}</div>
                                </template>
                            </el-table-column>
                            <el-table-column label="采购单号" width="130px">
                                <template slot-scope="{row,$idnex}">
                                    <div class="pd5 h35 bb-da-aaa" v-for="item in row.car_list">{{item.purchase_node}}</div>
                                </template>
                            </el-table-column>
                            <el-table-column label="vin" width="130px">
                                <template slot-scope="{row,$idnex}">
                                    <div class="pd5 h35 bb-da-aaa" v-for="item in row.car_list">{{item.vin_number}}</div>
                                </template>
                            </el-table-column>
                        </template>
                        
                        <el-table-column label="操作" width="180" align="center" fixed="right">
                            <template slot-scope="{row, $index}">
                                <template v-if="form.sale_info.stage_id==3 && form.sale_info.work_status==1">
                                    <div class="pd5 h35 bb-da-aaa" v-for="(item,indexChild) in row.car_list">
                                        <el-button type="text" icon="el-icon-view" @click="handleInvoice(row, $index,indexChild)">提交发票</el-button>
                                    </div>
                                </template>
                                
                                <!-- <template v-if="form.sale_info.stage_id==2 && form.sale_info.work_status==1">
                                    <el-button type="text" icon="el-icon-view" @click="handleDelivery(row, $index)">指定</el-button>
                                    <el-button type="text" icon="el-icon-delete" class="red" @click="">新建采购</el-button>
                                </template> -->
                                <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)" v-if="isShowTable11()">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class="toolbar ta-r mt10">
                        <span>共选择<b class="color-red fs-16"> {{goodsTotal}} </b>件商品</span>
                        <div class="input-coupon-sum">
                            优惠金额：
                            <el-input placeholder="请输入内容" v-model="form.sale_info.discount_amount" clearable :disabled="isDisabledBase()"></el-input>
                        </div>
                        <el-tag type="danger">￥：{{grossAmount}}</el-tag>
                    </div>
                </el-collapse-item>

                <el-collapse-item title="　收款信息" name="3">

                    <el-collapse v-model="collapseGatheringActive">

                        <el-collapse-item title="　定金阶段" name="1">

                            <el-form-item label="客户付款对象">
                                <el-radio-group v-model="form.dj.pay_object" @change="getExecUserNodeId" :disabled="isDisabledReceipt1()">
                                    <el-radio label="2">公司</el-radio>
                                    <el-radio label="3">其他销售方</el-radio>
                                </el-radio-group>
                                <el-tag type="danger" class="ml10">如客户将定金直接缴纳给其他4S店则选择其他销售方</el-tag>
                            </el-form-item>

                            <template v-if="form.dj.pay_object == 2">
                                <el-form-item label="客户支付方式">
                                    <el-radio-group v-model="form.dj.pay_method" @change="getExecUserNodeId" :disabled="isDisabledReceipt1()">
                                        <el-radio label="2">现付</el-radio>
                                        <el-radio label="3">转账</el-radio>
                                    </el-radio-group>
                                </el-form-item>

                                <el-form-item label="费用金额" prop="dj.cost_amount">
                                    <el-input v-model="form.dj.cost_amount" :disabled="isDisabledReceipt1()"></el-input>
                                </el-form-item>

                                <el-form-item label="费用凭证" prop="dj.cost_voucher_img">
                                    <el-upload
                                        :disabled="isDisabledReceipt1()"
                                        class="avatar-uploader"
                                        :action="IMG_UPLOAD_URL"
                                        :show-file-list="false"
                                        :on-success="handleUploadSuccess_dj_cost_voucher_img"
                                        :before-upload="handleBeforeUpload">
                                        <img v-if="form.dj.cost_voucher_img" :src="form.dj.cost_voucher_img" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                            </template>
                            <template v-else>
                                <el-form-item label="供应商" prop="dj.supplier">
                                    <el-select v-model="form.dj.supplier" placeholder="请选择供应商" :disabled="isDisabledReceipt1()">
                                        <el-option
                                            v-for="item in optionsSupplier"
                                            :key="item.id"
                                            :label="item.supplier_name"
                                            :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="费用金额" prop="dj.cost_amount">
                                    <el-input v-model="form.dj.cost_amount" :disabled="isDisabledReceipt1()"></el-input>
                                </el-form-item>
                            </template>

                            
                            <el-form-item label="备注" prop="dj.sale_remarkes">
                                <el-input type="textarea" v-model="form.dj.sale_remarkes" :disabled="isDisabledReceipt1()"></el-input>
                            </el-form-item>

                            <el-form-item label="选择审核人" prop="sale_info.first_exec_id" v-if="isShowFirstAudit()">
                                <el-select v-model="form.sale_info.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.sale_info.first_exec_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>

                        </el-collapse-item>

                        <el-collapse-item title="　购车款阶段" name="2" v-if="form.sale_info.stage_id!=1">
                            
                            <el-form-item label="购车方案">
                                <el-radio-group v-model="form.gc.pay_type" @change="getExecUserNodeId" :disabled="isDisabledReceipt2()">
                                    <el-radio label="2">全款</el-radio>
                                    <el-radio label="3">自贷</el-radio>
                                    <el-radio label="4">银行贷/助贷</el-radio>
                                </el-radio-group>
                            </el-form-item>

                            <template v-if="form.gc.pay_type!=2">
                                <el-form-item label="客户支付方式">
                                    <el-radio-group v-model="form.gc.pay_method" @change="getExecUserNodeId" :disabled="isDisabledReceipt2()">
                                        <el-radio label="2">现金</el-radio>
                                        <el-radio label="3">转账</el-radio>
                                    </el-radio-group>
                                </el-form-item>

                                <el-form-item label="选择信贷公司" prop="gc.credit_cp_id">
                                    <el-select v-model="form.gc.credit_cp_id" placeholder="请选择信贷公司" :disabled="isDisabledReceipt2()">
                                        <el-option
                                            v-for="item in optionsCompanyCredit"
                                            :key="item.id"
                                            :label="item.mc_name"
                                            :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="首付比例" prop="gc.payment_ratio">
                                    <el-input v-model="form.gc.payment_ratio" :disabled="isDisabledReceipt2()"><i slot="suffix">%</i></el-input>
                                </el-form-item>

                                <el-form-item label="贷款金额" prop="gc.credit_amount">
                                    <el-input v-model="form.gc.credit_amount" :disabled="isDisabledReceipt2()"></el-input>
                                </el-form-item>
                            </template>
                            
                            <el-form-item label="费用金额" prop="gc.cost_amount">
                                <el-input v-model="form.gc.cost_amount" :disabled="isDisabledReceipt2()"></el-input>
                            </el-form-item>

                            <el-form-item label="费用凭证" prop="gc.cost_voucher_img">
                                <el-upload
                                    :disabled="isDisabledReceipt2()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_gc_cost_voucher_img"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.gc.cost_voucher_img" :src="form.gc.cost_voucher_img" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </el-form-item>
                            
                            <el-form-item label="备注" prop="gc.sale_remarkes">
                                <el-input type="textarea" v-model="form.gc.sale_remarkes" :disabled="isDisabledReceipt2()"></el-input>
                            </el-form-item>

                        </el-collapse-item>

                        <el-collapse-item title="　服务费阶段" name="3" v-if="this.form.sale_info.stage_id>2">
                            
                            <el-form-item label="包含服务费">
                                <el-radio-group v-model="form.fwf.ser_charge" :disabled="isDisabledReceipt3()">
                                    <el-radio label="2">包含</el-radio>
                                    <el-radio label="3">不包含</el-radio>
                                </el-radio-group>
                            </el-form-item>

                            <template v-if="form.fwf.ser_charge == 2">
                                <el-form-item label="客户支付方式">
                                    <el-radio-group v-model="form.fwf.pay_method" @change="getExecUserNodeId" :disabled="isDisabledReceipt3()">
                                        <el-radio label="2">现金</el-radio>
                                        <el-radio label="3">转账</el-radio>
                                    </el-radio-group>
                                </el-form-item>

                                <el-form-item label="服务费金额">
                                    <el-input v-model="form.fwf.cost_amount" :disabled="isDisabledReceipt3()"></el-input>
                                </el-form-item>

                                <el-form-item label="支付凭证">
                                    <el-upload
                                        :disabled="isDisabledReceipt3()"
                                        class="avatar-uploader"
                                        :action="IMG_UPLOAD_URL"
                                        :show-file-list="false"
                                        :on-success="handleUploadSuccess_fwf_cost_voucher_img"
                                        :before-upload="handleBeforeUpload">
                                        <img v-if="form.fwf.cost_voucher_img" :src="form.fwf.cost_voucher_img" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                            </template>
                            
                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.fwf.sale_remarkes" :disabled="isDisabledReceipt3()"></el-input>
                            </el-form-item>

                        </el-collapse-item>

                    </el-collapse>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="4" v-if="isShowAudit()">

                    <el-collapse v-model="collapseAuditActive">
                        <el-collapse-item title="　车辆定金阶段" name="1">
                            <el-card class="box-card mb15" v-for="item in form.exam_dj" :key="item.exec_user_id">
                                <div><span class="display-ib ta-r w80">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w80">审核意见：</span>{{item.exec_op_type}}</div>
                                <div><span class="display-ib ta-r w80">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w80">审核人ID：</span> {{item.exec_user_id}}</div>
                                <div><span class="display-ib ta-r w80">审核时间：</span>{{item.create_time | time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <el-collapse-item title="　车辆首付款/全款阶段" name="2" v-if="form.sale_info.stage_id!=1">
                            <el-card class="box-card mb15" v-for="item in form.exam_gc" :key="item.exec_user_id">
                                <div><span class="display-ib ta-r w80">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w80">审核意见：</span>{{item.exec_op_type}}</div>
                                <div><span class="display-ib ta-r w80">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w80">审核人ID：</span> {{item.exec_user_id}}</div>
                                <div><span class="display-ib ta-r w80">审核时间：</span>{{item.create_time | time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <el-collapse-item title="　车辆服务费阶段" name="3" v-if="form.sale_info.stage_id>2">
                            <el-card class="box-card mb15" v-for="item in form.exam_fwf" :key="item.exec_user_id">
                                <div><span class="display-ib ta-r w80">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w80">审核意见：</span>{{item.exec_op_type}}</div>
                                <div><span class="display-ib ta-r w80">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w80">审核人ID：</span> {{item.exec_user_id}}</div>
                                <div><span class="display-ib ta-r w80">审核时间：</span>{{item.create_time | time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <template v-if="isShowOpinion()">
                            <el-form-item label="审核意见" class="mt15">
                                <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                    <el-option label="通过" value="1"></el-option>
                                    <el-option label="驳回" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.remark"></el-input>
                            </el-form-item>
                            <el-form-item label="选择审核人" class="mt15" v-if="form.sale_info.node_id!=3 && form.sale_info.node_id!=2 && form.sale_info.node_id!=5 && form.sale_info.node_id!=7 && form.sale_info.node_id!=10 && form.sale_info.node_id!=12 && form.sale_info.node_id!=18">
                                <el-select v-model="form.sale_info.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.sale_info.after_user_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </template>

                        <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                            <el-select v-model="form.sale_info.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.sale_info.first_exec_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>

                    </el-collapse>
                </el-collapse-item>
            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-if="pageType != 'view'">提交</el-button>
                <el-button type="primary" @click="handleSubmit(2)" v-if="isShowSave()">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
    
        <!-- 选择车型 -->
        <el-dialog title="选择车型" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar v-if="hackReset" :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>
        
        <!-- 选择审核人 -->
        <el-dialog title="选择审核人" :visible.sync="dialogAuditor" width="50%">
            <el-form ref="filtersAuditor" :inline="true" :model="filtersAuditor">
                <el-form-item label="车辆品牌" :inline="true">
                    <el-select v-model="filtersAuditor.brand" placeholder="请选择">
                        <el-option label="车辆品牌">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogAuditor = false">取 消</el-button>
                <el-button type="primary" @click="">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 指定交付 -->
        <el-dialog title="指定交付" :visible.sync="dialogDelivery" width="50%">
            <el-table :data="dataDelivery" border>
                <el-table-column label="">
                    <template slot-scope="{row,$index}">
                        <el-radio v-model="radioDelivery":label="$index+1" @change="handleSelectDelivery(row,$index)"></el-radio>
                    </template>
                </el-table-column>
                <el-table-column label="商品信息">
                    <template slot-scope="{row, $index}">
                        <p>{{row.car_series}} {{row.car_brand}}</p>
                        <p>{{row.horse_power}} {{row.drive_form}} {{row.car_class}}</p>
                        <p>{{row.fuel_type}}</p>
                    </template>
                </el-table-column>
                <el-table-column prop="car_series" label="车辆系列">
                </el-table-column>
                <el-table-column prop="car_class" label="车辆类型">
                </el-table-column>
                <el-table-column prop="vin_number" label="VIN号">
                </el-table-column>
                <el-table-column prop="name" label="供应商名称">
                </el-table-column>
                <el-table-column prop="name" label="车辆状态">
                </el-table-column>
                <el-table-column prop="create_time" label="采购日期">
                </el-table-column>
            </el-table>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogDelivery = false">取 消</el-button>
                <el-button type="primary" @click="handleSubmitDelivery">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 提交发票 -->
        <el-dialog title="提交发票" :visible.sync="dialogInvoice" width="50%">
            <el-form ref="filtersInvoice" :model="filtersInvoice" label-width="100px">
                <el-form-item label="车辆商品">
                    <p>{{filtersInvoice.car_series}} {{filtersInvoice.car_brand}}</p>
                    <p>{{filtersInvoice.horse_power}} {{filtersInvoice.drive_form}} {{filtersInvoice.car_class}}</p>
                    <p>{{filtersInvoice.fuel_type}}</p>
                </el-form-item>
                <el-form-item label="VIN">
                    {{filtersInvoice.vin_number}}
                </el-form-item>
                <el-form-item label="销售发票">
                    <el-upload
	                    class="avatar-uploader"
	                    :action="IMG_UPLOAD_URL"
	                    :show-file-list="false"
	                    :on-success="handleUploadSuccessInvoice"
	                    :before-upload="handleBeforeUpload">
	                    <img v-if="filtersInvoice.invoice_img" :src="filtersInvoice.invoice_img" class="avatar">
	                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
	                </el-upload>
                </el-form-item>
                <el-form-item label="销售发票号">
                    <el-input v-model="filtersInvoice.invoice_num" placeholder="请输入销售发票号"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogInvoice = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitInvoice">确 定</el-button>
            </span>
        </el-dialog>
        
    </div>
</template>

<script>
    import * as API from '@/api/salesManagement'
    import { apiNewCarList } from '@/api/resourceManagement'
    import selectCar from '@/components/selectCar'
    import selectClient from '@/components/selectClient'
    import { requestUrl } from '@/api/config'
    export default {
        name: 'baseform',
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                collapseGatheringActive: ['1'],
                dataDelivery: [{
                    name: ''
                }],
                pageType: '',
                radioDelivery: '',
                
                itemDelivery: {},
                loading: true,
                isShowDialogInvoice: false,
                dialogAuditor: false,
                dialogDelivery: false,
                dialogInvoice: false,
                couponSum: 0,
                execUserNodeId: 1,
                visiblePayment: false,
                visibleEarnest: false,
                hackReset: false,
                dialogSelectCarType: false,
                selectCarOptions:{
                    tableConfig:{
                        car_info: true,
                        yanse: true,
                        gonggao: true,
                        danjia: true,
                    },
                    formConfig: {
                        val: false
                    },
                    pageType: 'salesManagement',
                    isCheckbox: true
                    
                },
                filtersAuditor: {
                    brand:'',
                    series:'',
                    type:'',
                    drive:''
                },
                activeDelivery: '',
                filtersDelivery: {
                    brand:'',
                    series:'',
                    type:'',
                    drive:''
                },
                activeInvoice: '',
                activeInvoiceChild: '',
                filtersInvoice: {
                    car_series: '',
                    car_brand: '',
                    horse_power: '',
                    drive_form: '',
                    car_class: '',
                    fuel_type: '',
                    vin_number: '',
                    invoice_img: '',
                    invoice_num: '',
                },
                form: 
                {
                    opinion: '',
                    company: '',
                    auditor: '',
                    remark: '',
                    sale_info: {
                        cp_id: "",
                        car_order_id: "",
                        cus_user_id: "",
                        type: "1",
                        user_id: "",
                        first_exec_id: "",
                        after_user_id: "",
                        sale_num_code: "",
                        sale_body_id: "",
                        is_affiliat: "2",
                        cp_affiliat_id: "",
                        status: "1",
                        operater_id: "",
                        node_id: "1",
                        discount_amount: "",
                        create_time: "",
                        update_time: "",
                        node_name: "",
                        work_id: "1",
                        work_status: "1",
                        work_status_name: "待提交",
                        stage_id: "1",
                        stage_name: "定金阶段"
                    },
                    cus_info: {
                        id_card: "",
                        name: "",
                        gender: "",
                        mobile: "",
                        home_address: "",
                        cus_credit: []
                    },
                    car_info: {
                        rows: [],
                        total: "0",
                        page: 1,
                        offset: 10
                    },
                    dj: {
                        sale_info_id: "1",
                        sale_invoice: "1",
                        ser_charge: "2",
                        cheques_type: "2",
                        pay_type: "3",
                        pay_method: "2",
                        pay_object: "2",
                        is_credit: "1",
                        credit_cp_id: "",
                        payment_ratio: "",
                        supplier: "",
                        credit_amount: "",
                        payment_money: "",
                        cost_amount: "",
                        cost_voucher_img: '',
                        user_id: "",
                        sale_remarkes: "",
                        operater_id: "",
                        create_time: "",
                        update_time: "",
                        status: "",
                        supplier_name: ""
                    },
                    gc: {
                        sale_invoice: "1",
                        ser_charge: "2",
                        cheques_type: "3",
                        pay_type: "3",
                        pay_method: "2",
                        pay_object: "2",
                        is_credit: "1",
                        credit_cp_id: "",
                        payment_ratio: "",
                        supplier: "1",
                        credit_amount: "",
                        payment_money: "",
                        cost_amount: "",
                        cost_voucher_img: '',
                        user_id: "",
                        sale_remarkes: "",
                        operater_id: "",
                        create_time: "",
                        update_time: "",
                        status: "",
                        supplier_name: ""
                    },
                    fwf: {
                        sale_info_id: "1",
                        sale_invoice: "1",
                        ser_charge: "2",
                        cheques_type: "4",
                        pay_type: "3",
                        pay_method: "2",
                        pay_object: "2",
                        is_credit: "1",
                        credit_cp_id: "",
                        payment_ratio: "",
                        supplier: "1",
                        credit_amount: "1",
                        payment_money: "1",
                        cost_amount: "1",
                        cost_voucher_img: '',
                        user_id: "1",
                        sale_remarkes: "1",
                        operater_id: "1",
                        create_time: "1",
                        update_time: "1",
                        status: "1",
                        supplier_name: "abc"
                    },
                    exam_dj:[
                        {
                            create_time: "",
                            current_node_id: "",
                            exec_content: "",
                            exec_op_type: "",
                            exec_user_id: "",
                            exec_user_name: "",
                            id: "",
                            node_name: "",
                        }
                    ],
                    exam_gc:[],
                    exam_fwf:[],
                },
                optionsSupplier:[],
                optionsSaleBody:[],
                optionsExecUser:[],
                optionsCompany:[],
                optionsCompanyCredit:[],
                rules:{
                    'sale_info.sale_body_id': [
                        { required: true, message: '请选择销售员', trigger: 'change' }
                    ],
                    'dj.supplier': [
                        { required: true, message: '请选择供应商', trigger: 'change' }
                    ],
                    'dj.cost_amount': [
                        { required: true, message: '请输入费用金额', trigger: 'blur' }
                    ],
                    'dj.cost_voucher_img': [
                        { required: true, message: '请上传图片', trigger: 'change' }
                    ],
                    'dj.sale_remarkes': [
                        { required: true, message: '请输入备注', trigger: 'blur' }
                    ],
                    'sale_info.first_exec_id': [
                        { required: true, message: '请选择审核人', trigger: 'change' }
                    ],
                    'gc.credit_cp_id': [
                        {required:true, message:'请选择信贷公司', trigger: 'blur'}
                    ],
                    'gc.payment_ratio': [
                        {required:true, message:'请输入', trigger: 'blur'}
                    ],
                    'gc.credit_amount': [
                        {required:true, message:'请输入', trigger: 'blur'}
                    ],
                    'gc.cost_amount': [
                        {required:true, message:'请输入', trigger: 'blur'}
                    ],
                    'gc.cost_voucher_img': [
                        {required:true, message:'请输入', trigger: 'blur'}
                    ],
                    'gc.sale_remarkes': [
                        {required:true, message:'请输入', trigger: 'blur'}
                    ],
                }
                
            }
        },
        components:{
            selectCar,
            selectClient
        },
        computed:{
            grossAmount(){
                let zong = 0;
                this.form.car_info.rows.forEach((item, index)=>{
                    zong += Number(item.sale_price)*item.num
                })
                return zong-this.form.sale_info.discount_amount;
            },
            jijia(row){
                return (row) => {
                    return row.num*row.sale_price
                }
            },
            goodsTotal(){
                let zong = 0;
                this.form.car_info.rows.forEach((item, index)=>{
                    zong += 1*item.num
                })
                return zong;
            }
            
        },
        mounted(){
            this.getFormData()
        },
        methods: {
            
            isDisabledBase(){
                if(this.pageType=='view'){
                    return true
                }
                return !(this.form.sale_info.stage_id==1 && this.form.sale_info.work_status==1)
            },
            isDisabledReceipt1(){
                if(this.form.sale_info.stage_id == 1 && this.pageType!='view'){
                    return this.form.sale_info.work_status==2 || this.form.sale_info.work_status==4
                }else{
                    return true
                }
            },
            isDisabledReceipt2(){
                if(this.form.sale_info.stage_id == 2 && this.pageType!='view'){
                    return this.form.sale_info.work_status==2 || this.form.sale_info.work_status==4
                }else{
                    return true
                }
            },
            isDisabledReceipt3(){
                if(this.form.sale_info.stage_id == 3 && this.pageType!='view'){
                    return !(this.form.sale_info.work_status==1 || this.form.sale_info.work_status==4)
                }else{
                    return true
                }
            },
            isShowTable11(){
                return this.form.sale_info.stage_id==1 && this.form.sale_info.work_status==1
            },
            isShowTable12(){
                return this.form.sale_info.stage_id==1 && this.form.sale_info.work_status!=1
            },
            isShowTable21(){
                return this.form.sale_info.stage_id==2 && this.form.sale_info.work_status==1
            },
            isShowTable22(){
                return this.form.sale_info.stage_id==2 && this.form.sale_info.work_status!=1
            },
            isShowTable31(){
                return this.form.sale_info.stage_id==3 && this.form.sale_info.work_status==1
            },
            isShowCredit(){
                if(this.form.sale_info.stage_id!=1){
                    return !(this.form.sale_info.stage_id==2 && this.form.sale_info.stage_id==1)
                }
                return true
            },
            isShowAudit(){
                if(this.form.sale_info.stage_id==1){
                    return this.form.sale_info.work_status!=1
                }else{
                    return true
                }
            },
            isShowFirstAudit(){
                return this.form.sale_info.stage_id==1 && this.form.sale_info.work_status==1
                
            },
            isShowLastAudit(){
                if(this.form.sale_info.work_status==1){
                    return this.form.sale_info.node_id!=2 && this.form.sale_info.node_id!=3
                }
            },
            isShowOpinion(){
                return this.form.sale_info.work_status==2 || this.form.sale_info.work_status==3
            },
            isShowAddBtn(){
                return this.form.sale_info.stage_id==1 && this.form.sale_info.work_status==1
            },
            isShowSave(){
                if(this.pageType != 'view'){
                    return this.form.sale_info.work_status==1 || this.form.sale_info.work_status==3
                }
            },
            getFormData() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                    _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'
                if(_query.stage_id){
                    this.form.sale_info.stage_id = _query.stage_id
                }
                // 获取客户信息
                if(_query.user_id){
                    this.form.sale_info.cus_user_id = _query.user_id
                    API.salesManagementUserInfo({id:_query.user_id, type: _query.user_type}).then(res => {
                        this.loading = false
                        if(res.code == 0){
                            Object.assign(this.form.sale_info, res.data);
                            this.getExecUserNodeId()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }

                // 供应商
                apiNewCarList({}).then(res => {
                    if(res.code == 0){
                        this.optionsSupplier = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                // 公司列表
                API.salesManagementGetCompany({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                // 获取信息公司列表
                API.salesManagementGetCompanyCredit({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompanyCredit = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                // 销售人
                API.salesManagementGetSaleUser({}).then(res => {
                    if(res.code == 0){
                        this.optionsSaleBody = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                // 详情
                if(_query.id){
                    if(this.form.sale_info.stage_id==1){
                        API.salesManagementView({sale_id:_query.id}).then(res => {
                            this.loading = false
                            if(res.code == 0){
                                Object.assign(this.form, res.data);
                                this.getExecUserNodeId()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }else if(this.form.sale_info.stage_id==2){
                        API.salesManagementBuyCarInfo({sale_id:_query.id}).then(res => {
                            this.loading = false
                            if(res.code == 0){
                                Object.assign(this.form, res.data);
                                this.getExecUserNodeId()
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }else if(this.form.sale_info.stage_id==3){
                        API.salesManagementServiceInfo({sale_id:_query.id}).then(res => {
                            this.loading = false
                            if(res.code == 0){
                                Object.assign(this.form, res.data);
                                this.getExecUserNodeId()
                                this.isShowDialogInvoice = true
                            }else{
                                this.$message.error(res.msg)
                            }
                        })
                    }
                }
                
            },
            getExecUserNodeId(){
                let id = this.form.sale_info.stage_id
                if(id==1){
                    if(this.form.dj.pay_object == 2){
                        this.form.dj.pay_method==2?this.execUserNodeId=29:this.execUserNodeId=39
                    }else{
                        this.execUserNodeId = 35
                    }
                }else if(id==2){
                    if(this.form.gc.pay_type != 2){
                        // this.execUserNodeId = 30
                        this.form.gc.pay_method==2?this.execUserNodeId=29:this.execUserNodeId=39
                    }else{
                        this.execUserNodeId = 31
                    }
                }else{
                    this.form.fwf.pay_method==2?this.execUserNodeId=29:this.execUserNodeId=39
                }
                this.getExecUser();
                
            },
            getExecUser(){
                // 审核人
                let params = {
                    cp_id:this.form.sale_info.cp_id,
                    work_id:this.form.sale_info.work_id,
                    node_id:this.form.sale_info.work_status==1?this.execUserNodeId:this.form.sale_info.node_id,
                }
                this.form.sale_info.first_exec_id=''
                API.salesManagementGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSubmit(submitType) {
                this.$refs.form.validate((valid, errorMsg) => {
                    if(valid){
                        if(this.form.sale_info.stage_id==1){
                            if(this.form.sale_info.work_status==1 || this.form.sale_info.work_status==3){
                                let params = {
                                    type:this.$route.query.user_type,
                                    car_info: this.form.car_info.rows,
                                    info: {
                                        ...this.form.sale_info
                                    },
                                    receive: this.form.dj,
                                    submit_type: submitType
                                }
                                API.salesManagementAddSaleinfo(params).then(res => {
                                    if(res.code == 0){
                                        this.$message.success('提交成功！')
                                        this.$router.back()
                                    }else{
                                        this.$message.error(res.msg)
                                    }
                                })
                            }
                        }else if(this.form.sale_info.stage_id==2){
                            if(this.form.sale_info.work_status==1 || this.form.sale_info.work_status==3){
                                let params = {
                                    ...this.form.gc,
                                    sale_info_id: this.form.sale_info.sale_id,
                                    first_exec_id: this.form.sale_info.first_exec_id,
                                    submit_type: submitType
                                }
                                API.salesManagementAddBuyCarInfo(params).then(res => {
                                    if(res.code == 0){
                                        this.$message.success('提交成功！')
                                        this.$router.back()
                                    }else{
                                        this.$message.error(res.msg)
                                    }
                                })
                            }
                        }else if(this.form.sale_info.stage_id==3){
                            if(this.form.sale_info.work_status==1 || this.form.sale_info.work_status==3){
                                let params = {
                                    ...this.form.fwf,
                                    car_info: this.form.car_info.rows,
                                    sale_info_id: this.form.sale_info.sale_id,
                                    first_exec_id: this.form.sale_info.first_exec_id,
                                    submit_type: submitType
                                }
                                API.salesManagementAddServiceInfo(params).then(res => {
                                    if(res.code == 0){
                                        this.$message.success('提交成功！')
                                        this.$router.back()
                                    }else{
                                        this.$message.error(res.msg)
                                    }
                                })
                            }
                        }

                        // 审核提交
                        if(this.form.sale_info.work_status==2){
                            let auditType,
                                stageId=this.form.sale_info.stage_id
                            if(stageId==1){
                                auditType = 'sale'
                            }else if(stageId==2){
                                auditType = 'pay'
                            }else if(stageId==3){
                                auditType = 'server'
                            }else if(stageId==4){
                                auditType = 'refund'
                            }else if(stageId==5){
                                auditType = 'pur'
                            }else if(stageId==6){
                                auditType = 'pur_last'
                            }
                            let params = {
                                type: auditType,
                                id:this.form.sale_info.sale_id, //  订单id
                                exec_op_type: this.form.opinion, // 审核结果 1 同意 2 驳回
                                exam_remarks:this.form.remark, // 审核备注
                                after_user_id:this.form.sale_info.after_user_id, //   审核人
                                image_url:'',
                                submit_type: submitType
                            }
                            API.salesManagementSubmitSale(params).then(res => {
                                if(res.code == 0){
                                    this.$message.success('提交成功！')
                                    this.$router.back()
                                }else{
                                    this.$message.error(res.msg)
                                }
                                
                            })
                        }
                    }else{
                        return false
                    }
                })
                
            },
            handleSelectAuditor(val){
            	this.getExecUser();
            },
            handleDelete(row, index){
                this.form.car_info.rows.splice(index, 1);
            },
            handleDelivery(row, index){
                this.dialogDelivery = true
                this.activeDelivery = index
                API.salesManagementNoSaleCarList({}).then(res => {
                    if(res.code == 0){
                        this.dataDelivery = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })
            },

            handleSubmitDelivery(){
                this.dialogDelivery = false
                // console.log(this.form.car_info.rows[this.activeDelivery-1])
                Object.assign(this.form.car_info.rows[this.activeDelivery],this.itemDelivery)
            },
            handleSelectDelivery(row, index){
                this.itemDelivery = row
            },
            handleInvoice(row, index, indexChild){
                this.dialogInvoice = true
                this.activeInvoice = index
                this.activeInvoiceChild = indexChild
                this.filtersInvoice.vin_number = this.form.car_info.rows[index].car_list[indexChild].vin_number
                Object.assign(this.filtersInvoice,row)
            },
            vinNumber(item){
                
            },
            dialogSubmitInvoice(){
                this.dialogInvoice = false
                let item = {
                    invoice_img: this.filtersInvoice.invoice_img,
                    invoice_num: this.filtersInvoice.invoice_num,
                }
                Object.assign(this.form.car_info.rows[this.activeInvoice].car_list[this.activeInvoiceChild],item)
            },
            handleUploadSuccess_dj_cost_voucher_img(res, file, fileList){
                this.form.dj.cost_voucher_img = res.data.url
            },
            handleUploadSuccess_gc_cost_voucher_img(res, file, fileList){
                this.form.gc.cost_voucher_img = res.data.url
            },
            handleUploadSuccess_fwf_cost_voucher_img(res, file, fileList){
                this.form.fwf.cost_voucher_img = res.data.url
            },
            handleUploadSuccessInvoice(res, file, fileList){
                this.filtersInvoice.invoice_img = res.data.url
            },
            handleBeforeUpload(){

            },
            event_auditor() {
                this.dialogAuditor = true;
            },
            handleSelectCarType() {
                this.dialogSelectCarType = true
                this.hackReset = false
                this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                this.dialogSelectCarType = false;
                let arr = this.form.car_info.rows.concat(row)
                this.form.car_info.rows = arr
            }
        }
    }
</script>

